:root {
  font-family: "Commissioner", sans-serif;
}

:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --light-gray: hsl(0, 0%, 98%);
  --bookmarkBtn-background: hsl(0, 0%, 96%);
  --bookmarkBtn-oval-hover: #707070;
  --btn-hover-background: #147A73;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
}

img {
  object-fit: contain;
}

.large {
  display: none;
}

.med-desk {
  display: none;
}

@media (min-width: 40em) {
  .med-desk {
    display: initial;
  }
}
@media (min-width: 53.75em) {
  .large {
    display: initial;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-color: var(--light-gray);
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    padding: 3rem;
  }
}
@media (min-width: 53.75em) {
  .container {
    padding: 0;
    width: 77%;
    max-width: 69.375rem;
  }
}

.header {
  min-height: 18.75rem;
  background: url(../images/image-hero-mobile.jpg) no-repeat;
  background-size: cover;
}
.header__logo {
  z-index: 3;
  cursor: pointer;
}
.header__gradient {
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  height: 40%;
}
.header .navbar {
  z-index: 2;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .nav {
  position: absolute;
  right: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  z-index: 2;
  padding: 0 1.5rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  transition: all ease-in-out 0.3s;
}
.header .nav.open {
  top: 0;
}
.header .nav__list {
  list-style: none;
  width: 100%;
  margin-top: 5.5rem;
  background: white;
  padding: 0;
  border-radius: 0.5rem;
}
.header .nav__list .navlink:not(:last-child) {
  border-bottom: 1px solid rgba(47, 47, 47, 0.1);
}
.header .nav__list .navlink a {
  text-decoration: none;
  color: #000;
  display: inline-block;
  width: 100%;
  padding: 1.5rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.2;
}
.header__menuControls {
  z-index: 3;
  cursor: pointer;
}
.header__menuControls .closeMenu {
  display: none;
}
.header__menuControls .burger {
  display: inline-block;
}
.header__menuControls.open .closeMenu {
  display: inline-block;
}
.header__menuControls.open .burger {
  display: none;
}
@media (min-width: 40em) {
  .header {
    background: url(../images/image-hero-desktop.jpg) center no-repeat;
    min-height: 25rem;
  }
  .header__menuControls {
    display: none;
  }
  .header .navbar {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .header .nav {
    position: static;
    padding: 0;
    background: none;
    width: initial;
  }
  .header .nav__list {
    width: initial;
    margin: 0;
    background: none;
    border-radius: 0;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header .nav__list .navlink {
    border: none;
  }
  .header .nav__list .navlink a {
    color: white;
    font-size: 0.8125rem;
    line-height: 1rem;
    padding: 0.1875rem;
  }
  .header .nav__list .navlink a:hover, .header .nav__list .navlink a:focus {
    text-decoration: underline;
  }
}

.card {
  width: 100%;
  max-width: 28.125rem;
  margin: 0 auto 1.5rem;
  background-color: white;
  border-radius: 0.5rem;
  padding: 2.5rem 1.5rem;
  font-size: 0.875rem;
  line-height: 1.7;
  color: var(--dark-gray);
}
.card__heading {
  color: black;
  font-weight: 700;
}
.card__subHeading {
  color: var(--dark-cyan);
  font-weight: 500;
}
@media (min-width: 40em) {
  .card {
    max-width: 45.625rem;
    font-size: 1rem;
    width: 90%;
    padding: 3rem;
  }
}

.introCard {
  text-align: center;
  margin-top: -3.4375rem;
  padding-top: 3.25rem;
  position: relative;
}
.introCard .mastercraft-logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
.introCard h1 {
  margin: 0 auto 1rem;
  font-style: normal;
  font-weight: 700;
  font-size: 1.375rem;
  line-height: 1.2;
}
.introCard .description {
  margin-bottom: 1.5rem;
}
.introCard .button-cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 40em) {
  .introCard {
    margin-top: -5.75rem;
    padding-top: 3.5rem;
  }
  .introCard h1 {
    font-size: 1.75rem;
  }
  .introCard .description {
    margin-bottom: 2.5rem;
  }
}

.btn {
  font-size: 1rem;
  padding: 1.125em 2.625em;
  border-radius: 6.25rem;
  background-color: var(--moderate-cyan);
  color: white;
  font-style: normal;
  font-weight: bold;
  line-height: 1.25;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.btn.smallBtn {
  font-size: 0.875rem;
}
.btn:focus, .btn:hover {
  background-color: var(--btn-hover-background);
}

.bookmarkBtn {
  padding: 0;
  border: none;
  outline: none;
  color: var(--dark-gray);
  background-color: var(--bookmarkBtn-background);
}
.bookmarkBtn span {
  padding: 1.125em 1.5em 1.125em 1em;
}
.bookmarkBtn:hover {
  background-color: var(--bookmarkBtn-background);
}
.bookmarkBtn:hover svg circle {
  fill: var(--bookmarkBtn-oval-hover);
}

.analyticsCard .analytics {
  list-style: none;
  text-align: center;
}
.analyticsCard .analytics li:first-child {
  padding-top: 0;
}
.analyticsCard .analytics li:nth-child(2) {
  position: relative;
}
.analyticsCard .analytics li:nth-child(2)::before, .analyticsCard .analytics li:nth-child(2)::after {
  content: " ";
  display: inline-block;
  width: 5rem;
  height: 1px;
  position: absolute;
  transform: translateX(-50%);
  background-color: #000;
  opacity: 0.15;
  margin: 0 auto;
}
.analyticsCard .analytics__figure {
  padding-top: 1.5rem;
  margin-bottom: 0.5625rem;
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  line-height: 1.2;
  color: black;
}
.analyticsCard .analytics__detail {
  padding-bottom: 1.5rem;
  line-height: 1.2;
}
.analyticsCard progress {
  /* Reset the default appearance */
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 0.75rem;
  margin-top: 0.5rem;
}
.analyticsCard progress::-webkit-progress-bar {
  background-color: var(--light-gray);
  border-radius: 100px;
}
.analyticsCard progress::-webkit-progress-value {
  background-color: var(--moderate-cyan);
  border-radius: 100px;
}
@media (min-width: 53.75em) {
  .analyticsCard .analytics {
    display: flex;
    align-items: center;
    margin-bottom: 1.75rem;
  }
  .analyticsCard .analytics li {
    flex: 1;
    text-align: left;
  }
  .analyticsCard .analytics li:not(:first-child) {
    padding-left: 2.9375rem;
  }
  .analyticsCard .analytics li:nth-child(2)::before {
    height: 3.9375rem;
    width: 1px;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .analyticsCard .analytics li:nth-child(2)::after {
    height: 3.9375rem;
    width: 1px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .analyticsCard .analytics__figure {
    padding-top: 0;
  }
  .analyticsCard .analytics__detail {
    padding-bottom: 0;
  }
}